.tab__title__wrapper{
  display: flex;
  align-items: center;
  background-color: #e9ecef;
}

  .tab__title {
    cursor: pointer;
    padding: 5px 15px;
    font-weight: bold;
  }

    .tab__title:hover{
      background-color: #ccc;
    }

    .tab__title.active {
      background-color: #00000060;
      color: white;
    }

.tab__content__wrapper {
  padding: 8px 15px;
}

  .tab__content__wrapper.border {
    border: 1px solid #000000;
  }