.leftnav {
  width: 290px;
  background: #494ca2;
  height: 100%;
  color: white;
  position: fixed;
}

.leftnav__wrapper {
  display: flex;
  flex-direction: column;
  padding: 25px 25px;
  height: 100%;
}

.leftnav__title {
  display: flex;
  flex-direction: column;
}

.leftnav__title__prefix {
  font-size: 18px;
  font-weight: 700;
  margin: auto;
}

.leftnav__title__name {
  font-size: 30px;
  font-weight: 700;
  margin: auto;
}

.leftnav__items {
  margin-top: 25px;
  display: flex;
  flex-direction: column;
  color:#ffffff99
}

.leftnav__item {
  font-size: 16px;
  font-weight: 400;
  padding: 12px 5px;
  border-bottom: solid  1px;
  text-decoration: none;
  color:inherit
}

.leftnav__item.active, .leftnav__item:hover{
  color: white;
  font-weight: 600;
}

.leftnav__body {
  display: flex;
  flex-direction: column;
  flex: 1
}

.leftnav__footer {
  display: flex;
  flex-direction: column;
}