.masscell_th{
  width: 135px;
}

.masscell__div {
  position: relative;
}

.masscell__warning {
  flex:1;
}

.masscell__warningicon {
  color: #faad14;
  margin-right: 8px;
}

.masscell__value {
  display: flex;
  align-items: center;
  position:absolute;
  right: 5px;
}

.masstable__undo {
  margin: 3px 15px 0 0;
}

.masstable {
  min-width: 500px;
  /* width: auto;
  margin: 0 auto; */
}

.masstable__title__wrapper {
  display: flex;
  margin: auto;
  width: 430px;
}

.masstable__title__tool {
  margin-left: 15px;
}

.masstable__export {
  background-color: #faad14;
  border:none;
}

.massrow__fieldinput_wrapper {
  display: flex;
  align-items: center;
}

.massrow__fieldinput {
  flex: 1
}

.massrow__title__wrapper {
  display: flex;
  align-items: stretch;
}

.mass__modal__title__wrapper {
  display: flex;
  align-items: center;
}

.mass__subitemmodal__wrapper {
  padding: 5px 10px;
  height: 500px;
}

.mass__modal__title__selectsubitems__button
, .mass__modal__title__selectsubitems__button:hover {
  background-color: #52c41a;
  border: none;
}

.mass__groupname__wrapper{
  display: flex;
  justify-content: center;
  padding: 10px 0;
}

.mass__groupname__wrapper_dec {
  display: flex;
  align-items: center;
}

.mass__groupname__input {
  width: 350px;
}

.mass__groupname__button_wrapper {
  display: flex;
  margin: 0 0 0 10px;
}

.btn-warning, .btn-warning:hover {
  background-color: #faad14;
  border: none;
}

.btn-success, .btn-success:hover {
  background-color: #52c41a;
  border: none;
}

.mass__row__hovertool {
  display: flex;
  margin-left: 10px;
}

.massrow__addition__wrapper {
  display: flex;
  margin: 4px 0 0 8px;
}

.success {
  color: #52c41a;
}

.info {
  color: #1890ff;
}