.massdetail__wrapper {
  margin: 15px 0 0 0;
  display: flex;
}

.massdetail__addcolumn {
  margin: 4px 20px 0 0;
}

.massdetail__title__wrapper {
  display: flex;
  justify-content: center;
}

.massdetail__groupname__wrapper{
  display: flex;
  justify-content: center;
  padding: 10px 0;
}

.massdetail__groupname__wrapper_dec {
  display: flex;
  align-items: center;
}

.massdetail__groupname__input {
  width: 350px;
}

.massdetail__groupname__button_wrapper {
  display: flex;
  margin: 0 0 0 10px;
}

.massdetail__setting__wrapper {
  display: flex;
  align-items: center;
  margin: 0 0 30px 8px;
}

.massdetail__setting__select {
  width: 250px;
  margin-left: 12px;
}