.tc_table {
  height: 100%;
  width: 100%;
}

.tc_table th, .tc_table td {
  border: solid 2px #d6d9dc;
}

.tc_table__title {
  padding: 5px 0;
  display: flex;
  justify-content: center;
  font-size: 14px;
}

.tc_table td {
  margin: 0;
  padding: 0;
}

.tc_table tr {
  margin: 0;
  padding: 0;
}

.tc_select__partition__items {
  display: flex;
  padding: 10px 0;
  flex-direction: column;
  height: 100%;
  overflow: auto;
}

.tc_select__partition__items > span {
  padding-left: 20px;
}

.tc_select__partition__items > span:hover {
  background-color: #1890ff;
  color: white;
  font-weight: 500;
  cursor: pointer;
}